.before {
  background: rgba(0, 0, 0, 0.771);
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  /* justify-content: center; */
}

.before .content {
  background: rgba(255, 255, 255, 0.84);
  padding: 1rem;
  width: 100%;
  height: 70%;
  text-align: center;
}

.before button {
  width: 80px;
  height: 40px;
  background-color: rgb(255, 255, 255);
  margin-left:15px;
  margin-right:15px;
}

.before button:hover {
  background-color: rgb(92, 92, 92);
}
